@charset "UTF-8";
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Open Sans"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  box-sizing: border-box;
  height: 100%;
}

.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  grid-template-columns: 100%;
  min-height: 100%;
  margin: 0;
  padding: 0;
  min-width: 320px;
  color: #000000;
  background-color: #ffffff;
  font-family: "Open Sans", "Helvetica", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.container {
  padding: 0 20px;
}
@media (min-width: 1060px) {
  .container {
    width: 1060px;
    margin: 0 auto;
    padding: 0 60px;
  }
}

.header {
  background-color: #283645;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../../img/back-mobile.jpg");
  background-position: top center;
}
@media (min-resolution: 144dpi), (min-resolution: 2dppx) {
  .header {
    background-image: url("../../img/back-mobile@2x.jpg");
  }
}
@media (min-width: 660px) {
  .header {
    background-image: url("../../img/back-tablet.jpg");
  }
}
@media (min-width: 660px) and (min-resolution: 144dpi), (min-width: 660px) and (min-resolution: 2dppx) {
  .header {
    background-image: url("../../img/back-tablet@2x.jpg");
  }
}
@media (min-width: 1060px) {
  .header {
    background-image: url("../../img/back-desktop.jpg");
  }
}
@media (min-width: 1060px) and (min-resolution: 144dpi), (min-width: 1060px) and (min-resolution: 2dppx) {
  .header {
    background-image: url("../../img/back-desktop@2x.jpg");
  }
}

.header__wrapper {
  padding-top: 27px;
  padding-right: 20px;
  padding-left: 20px;
  min-height: 170px;
}
@media (min-width: 660px) {
  .header__wrapper {
    padding-top: 66px;
    min-height: 330px;
  }
}
@media (min-width: 1060px) {
  .header__wrapper {
    padding-top: 59px;
    min-height: 284px;
  }
}

.header__title {
  margin: 0;
  font-size: 24px;
  font-weight: 300;
  line-height: 33px;
  text-align: center;
  color: #ffffff;
}
@media (min-width: 660px) {
  .header__title {
    font-size: 50px;
    line-height: 68px;
  }
}
@media (min-width: 1060px) {
  .header__title {
    max-width: 1060px;
    margin: 0 auto;
    font-size: 50px;
    line-height: 68px;
  }
}

.logo {
  display: none;
  fill: #ffffff;
}
@media (max-width: 659px) {
  .logo--mobile {
    display: block;
  }
}
@media (min-width: 660px) and (max-width: 1059px) {
  .logo--tablet {
    display: block;
  }
}
@media (min-width: 1060px) {
  .logo--desktop {
    display: block;
  }
}

.nav {
  background-color: rgba(0, 0, 0, 0.3);
}
.nav--opened {
  background-color: #283645;
}
@media (min-width: 660px) {
  .nav {
    padding-top: 27px;
    padding-bottom: 43px;
  }
  .nav--opened {
    padding-top: 0;
    padding-bottom: 0;
  }
}

@media (min-width: 1060px) {
  .nav__wrapper {
    width: 1060px;
    margin: 0 auto;
    padding-left: 60px;
    padding-right: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.nav__toggle-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 20px;
}
.nav__toggle-wrapper--opened {
  border-bottom: 1px solid #000000;
}
@media (min-width: 660px) {
  .nav__toggle-wrapper {
    align-items: flex-end;
    padding-top: 0;
    padding-bottom: 0;
  }
  .nav__toggle-wrapper--opened {
    padding-top: 27px;
    padding-bottom: 27px;
  }
}
@media (min-width: 1060px) {
  .nav__toggle-wrapper {
    padding: 0;
  }
}

.nav__logo[href]:hover .logo, .nav__logo[href]:focus .logo {
  fill: #d22856;
}
.nav__logo[href]:active .logo {
  fill: #ffffff;
  opacity: 0.3;
}

.nav__toggle {
  position: relative;
  padding: 0;
  border: none;
  background-color: transparent;
}
.nav__toggle-image {
  fill: #ffffff;
}
.nav__toggle--opened .nav__toggle-image--closed {
  display: none;
}
.nav__toggle--closed .nav__toggle-image--opened {
  display: none;
}
.nav__toggle:hover .nav__toggle-image, .nav__toggle:focus .nav__toggle-image {
  fill: #d22856;
}
.nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 1060px) {
  .nav__toggle {
    display: none;
  }
}

@media (min-width: 1060px) {
  .nav__menu {
    margin-top: 13px;
  }
}

@media (max-width: 1059px) {
  .no-js .nav {
    padding-top: 0;
    padding-bottom: 0;
  }
}
@media (max-width: 1059px) {
  .no-js .nav__toggle-wrapper {
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: #283645;
    border-bottom: 1px solid #000000;
  }
}
.no-js .nav__toggle {
  display: none;
}

@media (max-width: 1059px) {
  .no-js .menu {
    display: block;
  }
}

.menu {
  display: none;
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style: none;
  background-color: #283645;
}
.menu--closed {
  display: block;
}
@media (min-width: 1060px) {
  .menu {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    max-width: 620px;
    padding-top: 14px;
    background-color: transparent;
  }
}

.menu__item {
  margin-right: 79px;
}
.menu__item:last-child {
  margin-right: 0;
}
@media (max-width: 1059px) {
  .menu__item {
    margin-right: 0;
    border-top: 1px solid rgba(255, 255, 255, 0.15);
    border-bottom: 1px solid #000000;
  }
}

.menu__link {
  position: relative;
  display: block;
  padding: 15px 0;
  color: #ffffff;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}
.menu__link[href]:hover, .menu__link[href]:focus {
  color: #d22856;
}
.menu__link[href]:active {
  color: rgba(210, 40, 86, 0.3);
}
@media (min-width: 1060px) {
  .menu__link {
    padding: 0;
    text-transform: none;
    font-weight: 400;
    line-height: 18px;
  }
  .menu__link[href]:active {
    color: rgba(255, 255, 255, 0.3);
  }
  .menu__link--active::before {
    content: "";
    position: absolute;
    bottom: -14px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
  }
}

.hero {
  position: relative;
  padding-top: 23px;
  overflow-x: hidden;
}
@media (min-width: 660px) {
  .hero {
    padding-top: 94px;
    padding-bottom: 83px;
  }
}
@media (min-width: 1060px) {
  .hero {
    width: 1060px;
    margin: 0 auto;
    padding: 60px;
    padding-top: 99px;
    padding-bottom: 94px;
  }
}

.hero__tagline {
