  font-size: 24px;
  font-weight: 300;
  line-height: 1.2;
  text-align: center;
}
@media (min-width: 660px) {
  .hero__tagline {
    max-width: 300px;
    margin-left: 0;
    margin-bottom: 2px;
    margin-right: 0;
    padding-left: 40px;
