  }
}
@media (min-width: 1060px) {
  .hero__tagline {
    padding-left: 0;
    font-size: 50px;
    line-height: 68px;
  }
}

.hero__image {
  display: block;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .hero__image {
    position: absolute;
    bottom: 0;
  }
}
@media (min-width: 1060px) {
  .hero__image {
    right: -66px;
  }
}

.hero__download {
  display: flex;
  justify-content: center;
  padding: 28px 20px 31px;
  background-color: #1d2631;
}
@media (min-width: 660px) {
  .hero__download {
    background-color: transparent;
    justify-content: start;
  }
}
@media (min-width: 1060px) {
  .hero__download {
    padding-left: 0;
    padding-right: 0;
    padding-top: 34px;
  }
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 55px;
  color: #ffffff;
  font: inherit;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  text-decoration: none;
  text-align: center;
  border: 3px solid #ffffff;
  border-radius: 55px;
  background-color: #f2f2f2;
  cursor: pointer;
}
@media (min-width: 1060px) {
  .button {
    font-size: 21px;
  }
}
.button:disabled {
  color: #ffffff;
  border-color: #e5e5e5;
  background-color: #e5e5e5;
  cursor: initial;
}
.button:disabled:hover, .button:disabled:focus, .button:disabled:active {
  color: #ffffff;
  border-color: #e5e5e5;
  background-color: #e5e5e5;
}
.button:hover, .button:focus {
  color: #d22856;
  background-color: #ffffff;
}
.button:active {
  color: rgba(210, 40, 86, 0.3);
  background-color: #ffffff;
}
.button--active {
  color: #d22856;
  border-color: #d22856;
}
.button--active:hover, .button--active:focus {
  color: #ffffff;
  background-color: #d22856;
}
.button--active:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--reset {
  color: #283645;
  border-color: #283645;
  opacity: 0.1;
}
.button--reset:hover, .button--reset:focus {
  color: rgba(40, 54, 69, 0.3);
  border-color: rgba(40, 54, 69, 0.3);
  opacity: 1;
  background-color: transparent;
}
.button--reset:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #283645;
  opacity: 0.3;
}
.button--success {
  min-height: 63px;
  border-color: #2aab6d;
  border-radius: 3px;
  text-transform: uppercase;
  background-color: #2aab6d;
}
.button--success:hover, .button--success:focus {
  color: #ffffff;
  border-color: #1c9b5e;
  background-color: #1c9b5e;
}
.button--success:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 660px) {
  .button--success {
    width: auto;
    padding: 0 58px;
  }
}
@media (min-width: 1060px) {
  .button--success {
    font-size: 18px;
  }
}

