.advantages {
  position: relative;
  overflow-y: hidden;
}
.advantages::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 786px;
  background-color: #f2f2f2;
  clip-path: polygon(100% 0, 100% 100%, 0 100%, 0 4%);
