.checkbox__indicator {
  position: relative;
  display: block;
  padding-left: 60px;
  padding-top: 5px;
  padding-bottom: 4px;
  font-size: 18px;
  line-height: 30px;
  cursor: pointer;
}
.checkbox__indicator::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 39px;
  height: 39px;
  background-image: url("../../img/icons/checkbox.svg");
  background-repeat: no-repeat;
}
.checkbox__indicator::after {
  content: "";
  position: absolute;
  display: none;
  top: 0;
  left: 0;
  width: 39px;
  height: 39px;
  background-image: url("../../img/icons/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.checkbox__input:hover + .checkbox__indicator::before, .checkbox__input:focus + .checkbox__indicator::before {
  background-image: url("../img/icons/checkbox-hover.svg");
}
.checkbox__input:active + .checkbox__indicator::before {
  background-image: url("../img/icons/checkbox-focus.svg");
}
.checkbox__input:checked + .checkbox__indicator::before {
  background-image: url("../img/icons/checkbox.svg");
}
.checkbox__input:checked + .checkbox__indicator::after {
  display: block;
  background-image: url("../img/icons/tick.svg");
}
.checkbox__input:checked:hover + .checkbox__indicator::before, .checkbox__input:checked:focus + .checkbox__indicator::before {
  background-image: url("../img/icons/checkbox-hover.svg");
}
.checkbox__input:checked:hover + .checkbox__indicator::after, .checkbox__input:checked:focus + .checkbox__indicator::after {
  background-image: url("../img/icons/tick.svg");
}
.checkbox__input:checked:active + .checkbox__indicator::before {
  background-image: url("../img/icons/checkbox-focus.svg");
}
.checkbox__input:checked:active + .checkbox__indicator::after {
  background-image: url("../img/icons/tick.svg");
}

